import { Check, User, Calendar, CreditCard } from 'lucide-react';

interface BookingWizardStepperProps {
    currentStep: number;
}

export default function BookingWizardStepper({ currentStep }: BookingWizardStepperProps) {
    const steps = [
        { id: 1, label: 'Data Diri', icon: User, desc: 'Identitas pemesan' },
        { id: 2, label: 'Jadwal', icon: Calendar, desc: 'Pilih tanggal main' },
        { id: 3, label: 'Bayar', icon: CreditCard, desc: 'Selesaikan transaksi' },
    ];

    return (
        <div className="w-full lg:w-72 flex-shrink-0">
            {/* CONTAINER UTAMA: Horizontal di HP, Vertikal di Desktop */}
            <div className="flex flex-row lg:flex-col justify-between items-start lg:gap-8 relative">
                
                {/* Garis Penghubung (Progress Line) - Desktop Only */}
                <div className="hidden lg:block absolute left-6 top-4 bottom-4 w-0.5 bg-gray-200 -z-10" />

                {steps.map((step, index) => {
                    const isActive = step.id === currentStep;
                    const isCompleted = step.id < currentStep;

                    return (
                        <div key={step.id} className="flex flex-col lg:flex-row items-center lg:items-start gap-3 lg:gap-4 flex-1 lg:flex-none w-full lg:w-auto z-10">
                            
                            {/* BULATAN NOMOR / ICON */}
                            <div 
                                className={`
                                    w-10 h-10 lg:w-12 lg:h-12 rounded-full flex items-center justify-center transition-all duration-300 border-2
                                    ${isActive 
                                        ? 'bg-[#D93F21] border-[#D93F21] text-white shadow-lg scale-110' 
                                        : isCompleted 
                                            ? 'bg-green-600 border-green-600 text-white' 
                                            : 'bg-white border-gray-200 text-gray-400'
                                    }
                                `}
                            >
                                {isCompleted ? (
                                    <Check className="w-5 h-5 lg:w-6 lg:h-6" />
                                ) : (
                                    <step.icon className="w-5 h-5 lg:w-6 lg:h-6" />
                                )}
                            </div>

                            {/* TEXT LABEL */}
                            <div className="text-center lg:text-left">
                                <p className={`text-xs lg:text-base font-bold transition-colors ${isActive ? 'text-[#D93F21]' : isCompleted ? 'text-green-600' : 'text-gray-500'}`}>
                                    {step.label}
                                </p>
                                {/* Deskripsi hanya muncul di Desktop agar HP tidak penuh */}
                                <p className="hidden lg:block text-xs text-gray-400 mt-1">
                                    {step.desc}
                                </p>
                            </div>

                            {/* Garis Penghubung Horizontal (Mobile Only) */}
                            {index !== steps.length - 1 && (
                                <div className={`lg:hidden h-1 w-full mt-4 mx-2 rounded-full ${isCompleted ? 'bg-green-600' : 'bg-gray-200'}`} />
                            )}
                        </div>
                    );
                })}
            </div>
        </div>
    );
}